class ApprovalsResponse {
    /** * The requested address. */
    address: string;
    /** * The timestamp when the response was generated. Useful to show data staleness to users. */
    updated_at: Date;
    /** * The requested quote currency eg: `USD`. */
    quote_currency: string;
    /** * The requested chain ID eg: `1`. */
    chain_id: number;
    /** * The requested chain name eg: `eth-mainnet`. */
    chain_name: string;
    /** * List of response items. */
    items: TokensApprovalItem[];

    constructor(data: ApprovalsResponse) {
        this.address = data.address;
        this.updated_at = data.updated_at && data.updated_at !== null ? parseISO(data.updated_at.toString()) : null;
        this.quote_currency = data.quote_currency;
        this.chain_id = data.chain_id;
        this.chain_name = data.chain_name;
        this.items = data.items && data.items !== null ? data.items.map((itemData: TokensApprovalItem) => new TokensApprovalItem(itemData)) : null;
    }
}
class TokensApprovalItem {
    /** * The address for the token that has approvals. */
    token_address: string;
    /** * The name for the token that has approvals. */
    token_address_label: string;
    /** * The ticker symbol for this contract. This field is set by a developer and non-unique across a network. */
    ticker_symbol: string;
    /** * Use contract decimals to format the token balance for display purposes - divide the balance by `10^{contract_decimals}`. */
    contract_decimals: number;
    /** * The contract logo URL. */
    logo_url: string;
    /** * The exchange rate for the requested quote currency. */
    quote_rate: number;
    /** * Wallet balance of the token. */
    balance: bigint | null;
    /** * Value of the wallet balance of the token. */
    balance_quote: number;
    /** * A prettier version of the quote for rendering purposes. */
    pretty_balance_quote: string;
    /** * Total amount at risk across all spenders. */
    value_at_risk: string;
    /** * Value of total amount at risk across all spenders. */
    value_at_risk_quote: number;
    /** * A prettier version of the quote for rendering purposes. */
    pretty_value_at_risk_quote: string;
    /** * Contracts with non-zero approvals for this token. */
    spenders: TokenSpenderItem[];

    constructor(data: TokensApprovalItem) {
        this.token_address = data.token_address;
        this.token_address_label = data.token_address_label;
        this.ticker_symbol = data.ticker_symbol;
        this.contract_decimals = data.contract_decimals;
        this.logo_url = data.logo_url;
        this.quote_rate = data.quote_rate;
        this.balance = data.balance && data.balance !== null ? BigInt(data.balance) : null;
        this.balance_quote = data.balance_quote;
        this.pretty_balance_quote = data.pretty_balance_quote;
        this.value_at_risk = data.value_at_risk;
        this.value_at_risk_quote = data.value_at_risk_quote;
        this.pretty_value_at_risk_quote = data.pretty_value_at_risk_quote;
        this.spenders = data.spenders && data.spenders !== null ? data.spenders.map((itemData: TokenSpenderItem) => new TokenSpenderItem(itemData)) : null;
    }
}
class TokenSpenderItem {
    /** * The height of the block. */
    block_height: number;
    /** * The offset is the position of the tx in the block. */
    tx_offset: number;
    /** * The offset is the position of the log entry within an event log." */
    log_offset: number;
    /** * The block signed timestamp in UTC. */
    block_signed_at: Date;
    /** * Most recent transaction that updated approval amounts for the token. */
    tx_hash: string;
    /** * Address of the contract with approval for the token. */
    spender_address: string;
    /** * Name of the contract with approval for the token. */
    spender_address_label: string;
    /** * Remaining number of tokens granted to the spender by the approval. */
    allowance: string;
    /** * Value of the remaining allowance specified by the approval. */
    allowance_quote: number;
    /** * A prettier version of the quote for rendering purposes. */
    pretty_allowance_quote: string;
    /** * Amount at risk for spender. */
    value_at_risk: string;
    /** * Value of amount at risk for spender. */
    value_at_risk_quote: number;
    /** * A prettier version of the quote for rendering purposes. */
    pretty_value_at_risk_quote: string;
    risk_factor: string;

    constructor(data: TokenSpenderItem) {
        this.block_height = data.block_height;
        this.tx_offset = data.tx_offset;
        this.log_offset = data.log_offset;
        this.block_signed_at = data.block_signed_at && data.block_signed_at !== null ? parseISO(data.block_signed_at.toString()) : null;
        this.tx_hash = data.tx_hash;
        this.spender_address = data.spender_address;
        this.spender_address_label = data.spender_address_label;
        this.allowance = data.allowance;
        this.allowance_quote = data.allowance_quote;
        this.pretty_allowance_quote = data.pretty_allowance_quote;
        this.value_at_risk = data.value_at_risk;
        this.value_at_risk_quote = data.value_at_risk_quote;
        this.pretty_value_at_risk_quote = data.pretty_value_at_risk_quote;
        this.risk_factor = data.risk_factor;
            
    }
}

